






























import Vue from 'vue';

const App = Vue.extend({
    data() {
        return {
            collapsed: true,
            isOnMobile: false,
            menu: [
                {
                    href: '/',
                    title: 'Dashboard',
                    icon: {
                        element: 'ion-icon',
                        attributes: {
                            name: 'home'
                        }
                    }
                },
                {
                    href: '/polling',
                    title: 'Polling',
                    icon: {
                        element: 'ion-icon',
                        attributes: {
                            name: 'stats-chart'
                        }
                    }
                },
                {
                    href: '/lists',
                    title: 'Party Lists',
                    icon: {
                        element: 'ion-icon',
                        attributes: {
                            name: 'list'
                        }
                    }
                },
                {
                    href: '/coalitions',
                    title: 'Coalition Builder',
                    icon: {
                        element: 'ion-icon',
                        attributes: {
                            name: 'trophy'
                        }
                    }   
                },
                {
                    href: '/electorates',
                    title: 'Electorates',
                    icon: {
                        element: 'ion-icon',
                        attributes: {
                            name: 'ribbon'
                        }
                    }
                }
            ]
        };
    },
    methods: {
        onToggleCollapse (collapsed: boolean) {
            this.collapsed = collapsed;
        }
    }
});
export default App;
